/*eslint-disable*/module.exports={messages:{"1. 21 million tokens are given to community users as an incentive cycle;":"1. 以2100万个token给到社区用户为一个激励周期；","2. Incentives are generated along with the user's storage behavior. If there is no storage, there will be no incentives;":"2. 激励是伴随着用户的存储行为产生的，没有存储，则没有激励；","3. In the first cycle, the excitation amount is 2100 each time it is stored;":"3. 第一个周期里，每次存储时，激励量是2100个；","4. When entering the next cycle, each excitation amount is half of the previous cycle;":"4. 进入下一个周期时，每次激励量是上个周期的一半；","5. With each user's token incentive, a 10% increment will be issued to the treasury address;":"5. 伴随每次用户的token激励，会有相应10%的增量下发至财政地址；","6. Using the displacement halving, the total amount of token incentives for a complete cycle is about 23.1 million;":"6. 采用位移减半，一个完整周期的token激励总量约为2310万个；","Add More":"继续添加","Address":"钱包地址","Arbitrum Mainnet":"Arbitrum主网","BTC Donate Address":"比特币捐赠地址","Bitcoin":"比特币","Bitcoin Address:":"比特币地址","Brain Wallet":"脑钱包","Bridge Model":"桥接模式","Cancel":"取消","Connect Wallet":"连接钱包","Docs":"文档","Donate Me":"捐赠","Download Zip":"本地运行","ETH(ERC20) Donate Address":"以太坊(ERC20)捐赠地址","Ethereum":"以太坊","Ethereum Address:":"以太坊地址:","Ethereum Mainnet":"以太坊主网","Forum":"论坛","Github":"源码","Hidden Model":"隐蔽模式","I want to mint seed incentive token":"存储时，我愿意参与激励通证铸造","Let's Generate":"开始生成","Let's Query":"开始查询","Let's Save":"开始存储","Let's Sign":"开始注册","License":"许可证","Meta Identity":"元身份","More Address":"更多地址","Please connect wallet firstly":"请您提前连接钱包","Please enter your password":"请输入您的密码","PrivateKey":"用户私钥","Query":"查找","Random Puzzle":"基于用户自输入内容","Rinkeby Test Network":"Rinkeby测试网","Save":"存储","Save Model:":"存储模式:","Sign Up":"注册","Submit":"提交","Switch":"切换","Token Address:":"通证地址:","Token Receiver:":"通证接收者:","User have to sign up a spacename before save or query content.":"在存储和查询之前，用户需要先在以太坊链上，注册保险库空间名称","Vault":"保险库","VaultName&Password":"基于已注册的保险库","Wallet":"钱包","version":"版本"}};